@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
:root {
  /* Screen Sizes */
  --mobile-width: 375px;
  --mobile-content-width: 375px;
  --desktop-width: 1366px;
  --desktop-content-width: 700px;
  /* Colours */
  --light-color: white;
  --dark-color: hsl(229, 25%, 31%);
  --score-color: hsl(229, 64%, 46%);
  --header-outline-color: hsl(217, 16%, 45%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-image: radial-gradient(
    circle at top,
    hsl(214, 47%, 23%),
    hsl(237, 49%, 15%)
  );
  background-size: 100%;
  background-repeat: no-repeat;

  font-family: 'Barlow Semi Condensed', sans-serif;

  min-height: 100vh;
  color: white;
  color: var(--light-color);
}

body {
  max-width: 375px;
  max-width: var(--mobile-content-width);
  height: 100vh;
  margin: auto;
}

#root {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 100%;
}

footer {
  display: flex;
  justify-content: center;
  width: 375px;
  width: var(--mobile-content-width);
  padding: 30px;
}

 /*
  * Desktop styles
  */
@media only screen and (min-width: 1366px) {
  body {
    width: 1366px;
    width: var(--desktop-width);
  }
  footer {
    justify-content: flex-end;
    width: 1366px;
    width: var(--desktop-width);
  }
}

header {
  width: var(--mobile-content-width);
  padding: 30px;
}

.title-bar {
  display: flex;
  width: 100%;
  border: 3px solid var(--header-outline-color);
  border-radius: 10px;
  padding: 17px;

  font-weight: 700;
  justify-content: space-between;
  align-items: center;
}

.title img {
  height: 48px;
}

/*
 * Desktop styles
 */
@media only screen and (min-width: 1366px) {
  header {
    display: flex;
    justify-content: center;
    width: var(--desktop-content-width);
  }
  .title-bar {
    width: var(--desktop-content-width);
  }
  .title img {
    height: 95px;
  }
}

.score-panel {
  display: inline-block;
  background-color: var(--light-color);
  color: var(--score-color);
  padding: 5px 20px;
  border-radius: 5px;
  height: 100%;

  text-transform: uppercase;
}
.score-panel p {
  text-align: center;
  margin: 0;
}
.score-title {
  font-size: 12px;
}
.score-display {
  color: var(--dark-color);
  font-weight: 700;
  font-size: 32px;
}

/*
 * Desktop styles
 */
@media only screen and (min-width: 1366px) {
  .score-panel {
    padding: 5px 40px;
  }
  .score-title {
    font-size: 18px;
  }
  .score-display {
    font-size: 64px;
  }
}

#gamePanel {
  max-width: var(--desktop-content-width);
  margin: auto;
}

.modal-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background-color: rgba(0,0,0,0.5);
}

.modal-dialog {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 25px;

  background-color: white;
}

.modal-close {
  border: none;
  background: transparent;
}

/*
 * Desktop styles
 */
@media only screen and (min-width: 1366px) {
  .modal-dialog {
    position: relative;
    top: 50%;
    left: 50%;
    width: 50%;
    height: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    border-radius: 10px;
    background-color: white;
  }
}

.modal-trigger {
  text-transform: uppercase;

  width: 130px;
  height: 42px;

  color: white;
  background: transparent;
  border: 2px solid white;
  border-radius: 10px;
}

.modal-dialog {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  background-color: white;
}

.rules-title {
  order: 1;
  color: var(--dark-color);
  text-transform: uppercase;
  text-align: center;
}

.rules-image {
  order: 2;
  display: block;
  object-fit: contain;
  margin: 20px 0;
}

.rules-close {
  order: 3;
}

@media only screen and (min-width: 1366px) {
  .modal-dialog {
    position: relative;
    flex-flow: row wrap;

    width: 400px;
    height: auto;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .rules-image {
    order: 3;
    margin: 20px auto;
  }
  .rules-close {
    order: 2;
  }
}

